<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>展示友情链接列表信息</li>
      <li>可对信息进行增加、删除、修改、启用和禁用管理信息</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label style="min-width:42px;">名称</label>
        <el-input v-model="queryParams.linkName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>URL</label>
        <el-input v-model="queryParams.linkUrl" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <cms-sticky>
    <section class="cms-batch">
      <cms-button  v-prem="'/friendly/save'" icon="add" type="success" size="small" @click="$routerLink('/friendly/save','save')">新增</cms-button>
      <cms-button  v-prem="'/friendly/delete'" icon="error" type="error" size="small" @click="deleteDatas(del)">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="ID" width="60"></el-table-column>
      <el-table-column prop="" label="LOGO" min-width="160">
        <div slot-scope="scope" class="table-img">
          <img :src="$cms.filterUrl(scope.row.resourcesSpaceData.url)" alt="mockimg">
        </div>
      </el-table-column>
      <el-table-column prop="linkName" label="名称" min-width="160">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.linkName">{{scope.row.linkName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="linkUrl" label="URL" min-width="200">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.linkUrl">{{scope.row.linkUrl}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="isEnable"  label="是否启用" min-width="110">
        <div slot-scope="scope">
          <cms-switch  v-model="scope.row.isEnable" onText='启用' offText='禁用' size='large'
            @change="toggleStatus(scope.row)">
          </cms-switch>
        </div>
      </el-table-column>
      <el-table-column prop="sortNum" label="排序" min-width="110">
        <template slot-scope="scope">
          <el-input v-model="scope.row.sortNum" maxlength="2" @keyup.enter.native="sortNum($event,scope.row.id)"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="updateTime" label="更新时间" min-width="170">
        <template slot-scope="scope">
          <span v-if="!scope.row.updateTime"> {{$moment(scope.row.createTime)}}</span>
          <span v-else>{{$moment(scope.row.updateTime)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button  v-prem="'/friendly/update'" icon='edit' size='small' type='info' @click="$routerLink('/friendly/update','update',scope.row)">编辑</cms-button>
          <cms-button  v-prem="'/friendly/delete'" icon='error' size='small' type='info' @click="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>

  </div>
</template>

<script>
import list from '@/mixins/list'
import { friendLinkPage, friendLinkDelete, linkIsEnable, linkSort } from '@/api/setting/friendlyLink.js'
var defaultData = {
  'linkName': '',
  'linkUrl': '',
  'page': 1,
  'size': 10
}
export default {
  name: 'friendlyLinkList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    sortNum(el, id) {
      // 数字排序
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        return false
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
          linkSort({ sorts: [{
            id: id,
            sortNum: val
          }] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage('修改失败')
            }
          })
        } else {
          el.target.className = 'cms-error el-input__inner'
          this.$cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'error',
            timer: '1000',
            fastClick: false
          })
        }
      }
    },
    preserveSort() { // 保持排序
      if (this.batchStatus) { // 若有选中项
        const params = []
        this.checkItems.forEach(v => { // 循环所以选中的项
          const tempObj = {
            id: v.id,
            sortNum: v.sortNum
          }
          params.push(tempObj)
        })
        linkSort({ sorts: params }).then(res => {
          if (res.code === 200) {
            this.$successMessage('保存排序成功')
            this.getTableData(friendLinkPage, this.queryParams)
          } else {
            this.$errorMessage(res.message)
          }
        })
      } else {
        this.$errorMessage('至少选择一条数据')
      }
    },
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(friendLinkDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(friendLinkDelete, this.ids) // 这个才是真实的删除方法
    },
    toggleStatus(item, val) {
      linkIsEnable({ id: item.id, isEnable: item.isEnable }).then(res => {
        if (res.code === 200) {
          if (item.isEnable) {
            this.$successMessage('启用成功')
          } else {
            this.$successMessage('禁用成功')
          }
          this.getTableData(friendLinkPage, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  activated() {
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      this.getTableData(friendLinkPage, this.queryParams)
    }
  },
  created() {
    this.getTableData(friendLinkPage, this.queryParams)
  }
}
</script>

<style>

</style>



